import AuthenticationService from './../../services/AuthenticationService'
import alerts from '@/alerts/alert'
import store from '@/store'
import confirmations from './confirmations'

const hccombenupdate = {
  namespaced: true,
  state: {
    data: [],
    loading: true,
    modal: false,
    detail: {},
    action: ''
  },
  getters: {
    getData (state) {
      return state.data
    }
  },
  mutations: {
    SET_DATA (state, data) {
      state.data = data
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    },
    SET_DATA_DETAIL (state, detail) {
      state.detail = detail
    },
    SET_MODAL (state, modal) {
      state.modal = modal
    },
    SET_ACTION (state, action) {
      state.action = action
    }
  },
  actions: {
    async reqData ({ commit }, payload) {
      commit('SET_LOADING', true)
      return AuthenticationService.post('cms/loan/HCCombenUpdateList', payload).then(({ data }) => {
        // console.log('reqHCCombenUpdate = ' + JSON.stringify(data))
        commit('SET_DATA', data.data)
        commit('SET_LOADING', false)
      })
    },
    async detail ({ commit }, payload) {
      commit('SET_ACTION', 'Detail')
      commit('SET_DATA_DETAIL', payload)
      commit('SET_MODAL', true)
      // await store.dispatch('hccombenupdate/first', payload)
    },
    async edit ({ commit }, payload) {
      commit('SET_ACTION', 'Edit')
      commit('SET_DATA_DETAIL', payload)
      commit('SET_MODAL', true)
      // await store.dispatch('hccombenupdate/first', payload)
    },
    async first ({ commit, state }, payload) {
      if (state.detail.id === payload.id) {
        commit('SET_MODAL', true)
      } else {
        commit('SET_DATA_DETAIL', [])
        commit('SET_LOADING', true)
        commit('SET_MODAL', true)
        return await AuthenticationService.get('cms/loan/HCCombenUpdateShow/' + payload.id).then(({ data }) => {
          commit('SET_DATA_DETAIL', data.data)
          // console.log('SET_DATA_DETAIL = ' + JSON.stringify(data.data))
          commit('SET_LOADING', false)
        }).catch(error => {
          console.log(error)
          commit('SET_LOADING', false)
        })
      }
    },
    simpan ({ commit, state }) {
      confirmations.confirmationApprove('hccombenupdate/lanjutkanSimpan', '_')
    },
    async lanjutkanSimpan ({ commit, state }) {
      const params = {
        ResignDate: state.detail.ResignDate,
        JobTitle: state.detail.jobtitle,
        NIK: state.detail.NIK,
        WorkLocation: state.detail.WorkLocation,
        Firstname: state.detail.firstname,
        Lastname: state.detail.lastname,
        PG: state.detail.PG,
        JG: state.detail.JG
      }
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return await AuthenticationService.postFormData('cms/loan/HCCombenUpdate/' + state.detail.id, params).then(({ data }) => {
        store.dispatch('hccomben/reqData')
        store.dispatch('loadings/setLoading', false)
        // console.log('SET_DATA_DETAIL = ' + JSON.stringify(data.data))
        commit('SET_LOADING', false)
        commit('SET_MODAL', false)
        alerts.sweetalertToast('success', 'Berhasil Diupdate')
      }).catch(error => {
        console.log(error)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
        commit('SET_LOADING', false)
      })
    }
  }
}

export default hccombenupdate
