// import router from '@/router'
import AuthenticationService from './../../services/AuthenticationService'
import alerts from '@/alerts/alert'
import store from '@/store'
import confirmations from './confirmations'
const approval = {
  namespaced: true,
  state: {
    data: [],
    loading: false,
    detail: {},
    modal: false
  },
  getters: {
    getData (state) {
      return state.data
    },
    getDetail (state) {
      return state.detail
    },
    getModalDetail (state) {
      return state.modal
    }
  },
  mutations: {
    SET_DATA (state, data) {
      state.data = data
    },
    SET_DETAIL (state, detail) {
      state.detail = detail
    },
    SET_BOOL_MODAL (state, bool) {
      state.modal = bool
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    }
  },
  actions: {
    async reqData ({ commit }) {
      commit('SET_LOADING', true)
      return await AuthenticationService.get('cms/loan/ApprovalList').then(({ data }) => {
        // console.log('approval = ' + JSON.stringify(data))
        commit('SET_DATA', data.data)
        commit('SET_LOADING', false)
      })
    },
    async handledetail ({ commit }, payload) {
      commit('SET_DETAIL', {})
      commit('SET_LOADING', true)
      commit('SET_BOOL_MODAL', true)
      return await AuthenticationService.get('cms/loan/ApprovalShow/' + payload.id).then(({ data }) => {
        commit('SET_DETAIL', data.data)
        commit('SET_LOADING', false)
      })
    },
    postSubmit ({ commit, state }, param) {
      if (param === '1') {
        confirmations.confirmationApprove('approval/lanjukanPostSubmit', param)
      } else if (param === '0') {
        confirmations.confirmationReject('approval/lanjukanPostSubmit', param)
      }
    },
    async lanjukanPostSubmit ({ commit, state }, param) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      const params = {
        Approval: param,
        ApprovalType: state.detail.ApprovalType,
        ApprovalBy: state.detail.NextApproval,
        ApprovalNo: state.detail.ApprovalNo,
        Seq: state.detail.Seq
      }
      return await AuthenticationService.post('cms/loan/ApprovalSubmit/' + state.detail.id, params).then(({ data }) => {
        store.dispatch('approval/reqData')
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        if (data.code === 200) {
          alerts.sweetalertToast('success', 'Berhasil diproses')
          commit('SET_BOOL_MODAL', false)
        }
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message)
        // console.log('Gagal Di tambahkan', error.response)
      })
    },
    dialogClose ({ commit }) {
      commit('SET_BOOL_MODAL', false)
    }
  }
}

export default approval
