import AuthenticationService from './../../services/AuthenticationService'
import alerts from '@/alerts/alert'
import confirmations from './confirmations'
import store from '@/store'

const loan = {
  namespaced: true,
  state: {
    data: [],
    loading: false,
    modal: false,
    detail: {
      pta1Cond: false,
      pta2Cond: false
    },
    action: ''
  },
  getters: {
    getData (state) {
      return state.data
    },
    getDataDetail (state) {
      return state.detail
    },
    getModal (state) {
      return state.modal
    },
    getAction (state) {
      return state.action
    }
  },
  mutations: {
    SET_DATA (state, data) {
      state.data = data
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    },
    SET_DATA_DETAIL (state, detail) {
      state.detail = detail
    },
    SET_MODAL (state, modal) {
      state.modal = modal
    },
    SET_ACTION (state, action) {
      state.action = action
    }
  },
  actions: {
    async reqData ({ commit }) {
      commit('SET_LOADING', true)
      return AuthenticationService.post('cms/loan/AdminList', store.getters['search/getSearch']).then(({ data }) => {
        commit('SET_DATA', data.data)
        commit('SET_LOADING', false)
      })
    },
    async edit ({ commit }, payload) {
      // alert(payload)
      commit('SET_ACTION', 'Edit')
      await store.dispatch('loan/first', payload)
    },
    async detail ({ commit }, payload) {
      commit('SET_ACTION', 'Detail')
      await store.dispatch('loan/first', payload)
    },
    async cairkan ({ commit }, payload) {
      commit('SET_ACTION', 'Cairkan')
      await store.dispatch('loan/first', payload)
    },
    async first ({ commit, state }, payload) {
      if (state.detail.id === payload.id) {
        commit('SET_MODAL', true)
      } else {
        commit('SET_DATA_DETAIL', [])
        commit('SET_LOADING', true)
        commit('SET_MODAL', true)
        return await AuthenticationService.get('cms/loan/AdminShow/' + payload.id).then(({ data }) => {
          commit('SET_DATA_DETAIL', data.data)
          if (data.data.pta1 !== null) {
            state.detail.pta1Cond = true
          } else {
            state.detail.pta1Cond = false
          }
          if (data.data.pta2 !== null) {
            state.detail.pta2Cond = true
          } else {
            state.detail.pta2Cond = false
          }
          // console.log('SET_DATA_DETAIL = ' + JSON.stringify(data.data))
          commit('SET_LOADING', false)
        }).catch(error => {
          console.log(error)
          commit('SET_LOADING', false)
        })
      }
    },
    simpan ({ commit }, Status) {
      if (Status === 'APV') {
        confirmations.confirmationApprove('loan/lanjutSimpan', Status)
      } else if (Status === 'RJT') {
        confirmations.confirmationReject('loan/lanjutSimpan', Status)
      }
    },
    async lanjutSimpan ({ commit, state }, Status) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      state.detail.Status = Status
      const params = {
        NIK: state.detail.NIK,
        LoanAdminDoc: state.detail.LoanAdminDoc,
        sisapokok: state.detail.sisapokok,
        sisabunga: state.detail.sisabunga,
        ApprovalBy: state.detail.ApprovalBy,
        ApprovalNo: state.detail.ApprovalNo,
        Seq: state.detail.Seq,
        Status: Status
      }
      return await AuthenticationService.post('cms/loan/AdminUpdate/' + state.detail.id, params).then(({ data }) => {
        // console.log(JSON.stringify(data))
        store.dispatch('loadings/setLoading', false)
        store.dispatch('loan/reqData')
        commit('SET_LOADING', false)
        commit('SET_MODAL', false)
        alerts.sweetalertToast('success', data.status)
      }).catch(error => {
        console.log(error)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
        commit('SET_LOADING', false)
      })
    },
    prosesCairkan ({ commit }) {
      confirmations.confirmationApprove('loan/LanjutkanProsesCairkan', '')
    },
    async LanjutkanProsesCairkan ({ commit, state }) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      const params = {
        loan_amount: state.detail.LoanAmount,
        term_years: state.detail.PaymentPeriode,
        interest: state.detail.EffectiveRate,
        terms: state.detail.Tenor
      }
      return await AuthenticationService.post('cms/loan/Activation/' + state.detail.id, params).then(({ data }) => {
        // console.log(JSON.stringify(data))
        store.dispatch('loadings/setLoading', false)
        store.dispatch('loan/reqData')
        commit('SET_LOADING', false)
        commit('SET_MODAL', false)
        alerts.sweetalertToast('success', data.status)
      }).catch(error => {
        console.log(error)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
        commit('SET_LOADING', false)
      })
    }
  }
}

export default loan
