// import router from '@/router'
import AuthenticationService from '@/services/AuthenticationService'
import alerts from '@/alerts/alert'
import store from '@/store'

const employee = {
  namespaced: true,
  state: {
    employees: [],
    loading: true,
    loadingButton: false,
    dialog: false,
    file: null
  },
  getters: {
    getEmployee (state) {
      return state.employees
    }
  },
  mutations: {
    SET_EMPLOYEES (state, employees) {
      state.employees = employees
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    },
    SET_LOADING_BUTTON (state, loadingButton) {
      state.loadingButton = loadingButton
    },
    SET_DIALOG (state, dialog) {
      state.dialog = dialog
    }
  },
  actions: {
    async reqEmployee ({ commit }) {
      commit('SET_LOADING', true)
      return await AuthenticationService.get('cms/Employee').then(({ data }) => {
        commit('SET_EMPLOYEES', data.data)
        commit('SET_LOADING', false)
      })
    },
    async aktifkan ({ commit }, id) {
      commit('SET_LOADING_BUTTON', true)
      store.dispatch('loadings/setLoading', false)
      return await AuthenticationService.get('cms/Employee/activation/' + id).then(({ data }) => {
        commit('SET_LOADING_BUTTON', true)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', 'Berhasil DiAktifkan')
      })
    },
    async upload ({ commit, state }) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      const data = new FormData()
      data.append('import_file', state.file)
      return await AuthenticationService.postFormData('cms/Employee/bulk', data).then(({ response }) => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        commit('SET_DIALOG', false)
        alerts.sweetalertToast('success', 'Berhasil Diupload')
        store.dispatch('employee/reqEmployee')
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    }
  }
}

export default employee
