// import router from '@/router'
import AuthenticationService from '@/services/AuthenticationService'
import alerts from '@/alerts/alert'
import store from '@/store'

const auth = {
  namespaced: true,
  state: {
    msgError: [],
    loading: false,
    captcha: {},
    token: null,
    user: null,
    form: {
      email: '',
      password: '',
      captcha: '',
      config: 'flat',
      key: ''
    }
  },
  getters: {
    getCaptcha (state) {
      return state.captcha
    },
    isAuthenticated: (state) => !!state.token,
    token: (state) => state.token,
    user: (state) => state.user
  },
  mutations: {
    SET_LOADING (state, loading) {
      state.loading = loading
    },
    SET_CAPTCHA (state, captcha) {
      state.captcha = captcha
    },
    SET_TOKEN (state, token) {
      state.token = token
    },
    SET_USER (state, user) {
      state.user = user
    },
    CLEAR_TOKEN_LS (state, token) {
      state.token = token
      localStorage.removeItem('auth-persist-state')
      localStorage.removeItem('key-slide-persist-state')
      localStorage.removeItem('profile-persist-state')
      localStorage.removeItem('token-persist-state')
    }
  },
  actions: {
    async forgotPassword ({ commit }, payload) {
      commit('SET_LOADING', true)
      return AuthenticationService.post('reset-password', payload).then(({ data }) => {
        commit('SET_LOADING', false)
      }).catch(error => {
        alerts.sweetalertToast('error', error.response.data.message)
        commit('SET_LOADING', false)
      })
    },
    async reqCaptcha ({ commit }) {
      return await AuthenticationService.get('get-key/flat').then(({ data }) => {
        commit('SET_CAPTCHA', data)
      })
    },
    async refreshToken ({ commit }, payload = []) {
      commit('SET_LOADING', true)
      return await AuthenticationService.post('cms/refresh', payload).then(({ data }) => {
        commit('SET_LOADING', false)
        commit('SET_TOKEN', data.data.access_token)
        commit('SET_USER', data.data.user)
      }).catch(error => {
        const token = null
        commit('SET_LOADING', false)
        commit('CLEAR_TOKEN_LS', token)
        alerts.sweetalertToast('error', error.response.data.error)
      })
    },
    async login ({ commit, state }) {
      commit('SET_LOADING', true)
      state.form.key = state.captcha.key
      state.form.captcha = state.form.captcha.toLowerCase()
      return await AuthenticationService.post('cms/login', state.form).then(({ data }) => {
        commit('SET_LOADING', false)
        commit('SET_TOKEN', data.data.access_token)
        commit('SET_USER', data.data.user)
        state.form.captcha = ''
        window.location = '/Dashboard'
        // router.push({ name: 'Dashboard' })
      }).catch(error => {
        store.dispatch('auth/reqCaptcha')
        commit('SET_LOADING', false)
        alerts.sweetalertToast('error', error.response.data.error)
        state.form.captcha = ''
      })
    },
    async LogOut ({ commit }) {
      const token = null
      commit('CLEAR_TOKEN_LS', token)
    }
  }
}

export default auth
