import AuthenticationService from '../../services/AuthenticationService'
import alerts from '@/alerts/alert'
import store from '@/store'

const dataiuran = {
  namespaced: true,
  state: {
    data: [],
    edit: {},
    loading: true,
    dialog: false,
    file: null
  },
  getters: {
    getData (state) {
      return state.data
    },
    getDataEdit (state) {
      return state.edit
    }
  },
  mutations: {
    SET_DATA (state, data) {
      state.data = data
    },
    SET_DATA_EDIT (state, data) {
      state.edit = data
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    }
  },
  actions: {
    async reqData ({ commit }) {
      commit('SET_LOADING', true)
      return AuthenticationService.get('cms/SaldoIuran').then(({ data }) => {
        // console.log('req dataiuran = ' + JSON.stringify(data))
        commit('SET_DATA', data.data)
        commit('SET_LOADING', false)
      })
    },
    async edit ({ commit }, payload) {
      commit('SET_DATA_EDIT', payload)
    },
    hapus ({ commit }, id) {
      alerts.questionDelete(id, 'dataiuran/lanjuthapus')
    },
    async lanjuthapus ({ commit }, id) {
      commit('SET_LOADING', true)
      return await AuthenticationService.get('cms/HCBP/delete/' + id).then(({ data }) => {
        store.dispatch('dataiuran/reqData')
        commit('SET_LOADING', false)
        alerts.sweetalertToast('success', 'Berhasil Dihapus')
      }).catch(error => {
        commit('SET_LOADING', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    },
    async upload ({ commit, state }) {
      commit('SET_LOADING', true)
      const data = new FormData()
      data.append('import_file', state.file)
      return await AuthenticationService.postFormData('cms/SaldoIuran/bulk', data).then(({ response }) => {
        commit('SET_LOADING', false)
        commit('SET_DIALOG', false)
        alerts.sweetalertToast('success', 'Berhasil Diupload')
        store.dispatch('dataiuran/reqData')
      }).catch(error => {
        commit('SET_LOADING', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    }
  }
}

export default dataiuran
