import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import SecureLS from 'secure-ls'

import dashboard from './modules/dashboard'
import auth from './modules/auth'
import profile from './modules/profile'
import employee from './modules/employee'
import sidebar from './modules/sidebar'
import product from './modules/product'
import hccomben from './modules/hccomben'
import hccombenupdate from './modules/hccombenupdate'
import generalsetting from './modules/generalsetting'
import promotion from './modules/promotion'
import blog from './modules/blog'
import organization from './modules/organization'
import faq from './modules/faq'
import datahcbp from './modules/datahcbp'
import dataiuran from './modules/dataiuran'
import approval from './modules/approval'
import fundingbatch from './modules/fundingbatch'
import hcbp from './modules/hcbp'
import loan from './modules/loan'
import pretermination from './modules/pretermination'
import transfer from './modules/transfer'
import loadings from './modules/loading'
import search from './modules/search'
import amortisasi from './modules/amortisasi'
import modalFile from './modules/modalFile'

Vue.use(Vuex)

const secureLS = new SecureLS({
  encodingType: 'aes',
  encryptionSecret: 'vue-auth-persist'
})

const authState = createPersistedState({
  key: 'auth-persist-state',
  paths: ['auth.captcha'],
  storage: {
    getItem: (key) => secureLS.get(key),
    setItem: (key, value) => secureLS.set(key, value),
    removeItem: (key) => secureLS.remove(key)
  }
})

const sidebarState = createPersistedState({
  key: 'sidebar-persist-state',
  paths: ['sidebar.sidebar'],
  storage: {
    getItem: (key) => secureLS.get(key),
    setItem: (key, value) => secureLS.set(key, value),
    removeItem: (key) => secureLS.remove(key)
  }
})

const keySlideState = createPersistedState({
  key: 'key-slide-persist-state',
  paths: ['auth.key'],
  storage: {
    getItem: (key) => secureLS.get(key),
    setItem: (key, value) => secureLS.set(key, value),
    removeItem: (key) => secureLS.remove(key)
  }
})

const profileState = createPersistedState({
  key: 'profile-persist-state',
  paths: ['auth.user'],
  storage: {
    getItem: (key) => secureLS.get(key),
    setItem: (key, value) => secureLS.set(key, value),
    removeItem: (key) => secureLS.remove(key)
  }
})

const tokenState = createPersistedState({
  key: 'token-persist-state',
  paths: ['auth.token'],
  storage: {
    getItem: (key) => secureLS.get(key),
    setItem: (key, value) => secureLS.set(key, value),
    removeItem: (key) => secureLS.remove(key)
  }
})

export default new Vuex.Store({
  modules: {
    auth: auth,
    dashboard: dashboard,
    profile: profile,
    employee: employee,
    sidebar: sidebar,
    product: product,
    hccomben: hccomben,
    hccombenupdate: hccombenupdate,
    generalsetting: generalsetting,
    promotion: promotion,
    blog: blog,
    organization: organization,
    faq: faq,
    datahcbp: datahcbp,
    dataiuran: dataiuran,
    approval: approval,
    fundingbatch: fundingbatch,
    hcbp: hcbp,
    loan: loan,
    pretermination: pretermination,
    transfer: transfer,
    loadings: loadings,
    search: search,
    amortisasi: amortisasi,
    modalFile: modalFile
  },
  plugins: [authState, tokenState, keySlideState, profileState, sidebarState]
})
