import router from '@/router'
import AuthenticationService from './../../services/AuthenticationService'
import alerts from '@/alerts/alert'
import store from '@/store'

const generalsetting = {
  namespaced: true,
  state: {
    data: [],
    edit: {},
    loading: false
  },
  getters: {
    getData (state) {
      return state.data
    },
    getDataEdit (state) {
      return state.edit
    }
  },
  mutations: {
    SET_DATA (state, data) {
      state.data = data
    },
    SET_DATA_EDIT (state, data) {
      state.edit = data
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    }
  },
  actions: {
    async reqData ({ commit }) {
      commit('SET_LOADING', true)
      return AuthenticationService.get('cms/generalsetting').then(({ data }) => {
        // console.log('req generalsetting = ' + JSON.stringify(data))
        commit('SET_DATA', data.data)
        commit('SET_LOADING', false)
      })
    },
    async edit ({ commit }, payload) {
      if (payload.GSName) {
        commit('SET_DATA_EDIT', payload)
        router.push({ name: 'Generaltupdate', params: { id: payload.id } })
      } else {
        commit('SET_LOADING', true)
        return AuthenticationService.get('cms/generalsetting/show/' + payload.id).then(({ data }) => {
          console.log('req generalsetting = ' + JSON.stringify(data))
          commit('SET_DATA_EDIT', data.data)
          commit('SET_LOADING', false)
        })
      }
    },
    async update ({ commit }, payload) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return AuthenticationService.post('cms/generalsetting/update/' + payload.id, payload).then(({ data }) => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', 'Berhasil diupdate,')
        router.push({ name: 'GeneralSetting' })
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    },
    async simpan ({ commit }, payload) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return AuthenticationService.post('cms/generalsetting/create', payload).then(({ data }) => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', 'Berhasil disimpan.')
        router.push({ name: 'GeneralSetting' })
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    }
  }
}

export default generalsetting
