// import router from '@/router'
import AuthenticationService from './../../services/AuthenticationService'
import store from '@/store'
import alerts from '@/alerts/alert'
import confirmations from './confirmations'

const hcbp = {
  namespaced: true,
  state: {
    data: [],
    loading: true,
    modal: false,
    detail: {},
    action: '',
    DokumenPersetujuan: null
  },
  getters: {
    getData (state) {
      return state.data
    },
    getDialogDetail (state) {
      return state.dialogDetail
    },
    getDataDetail (state) {
      return state.detail
    },
    getModal (state) {
      return state.modal
    },
    getAction (state) {
      return state.action
    }
  },
  mutations: {
    SET_DATA (state, data) {
      state.data = data
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    },
    SET_DIALOG_DETAIL (state, dialogDetail) {
      state.dialogDetail = dialogDetail
    },
    SET_DATA_DETAIL (state, detail) {
      state.detail = detail
    },
    SET_MODAL (state, modal) {
      state.modal = modal
    },
    SET_ACTION (state, action) {
      state.action = action
    }
  },
  actions: {
    async reqData ({ commit }) {
      commit('SET_LOADING', true)
      const params = {
        id: store.getters['auth/user'].id,
        id_company: store.getters['auth/user'].companyID
      }
      return AuthenticationService.post('cms/loan/HCBPList', params).then(({ data }) => {
        // console.log('fundingbatch = ' + JSON.stringify(data))
        commit('SET_DATA', data.data)
        commit('SET_LOADING', false)
      })
    },
    async detail ({ commit }, payload) {
      commit('SET_ACTION', 'Detail')
      await store.dispatch('hcbp/first', payload)
    },
    async edit ({ commit }, payload) {
      commit('SET_ACTION', 'Update')
      await store.dispatch('hcbp/first', payload)
    },
    async first ({ commit, state }, payload) {
      if (state.detail.id === payload.id) {
        commit('SET_MODAL', true)
      } else {
        commit('SET_DATA_DETAIL', [])
        commit('SET_LOADING', true)
        commit('SET_MODAL', true)
        return await AuthenticationService.get('cms/loan/HCBPShow/' + payload.id).then(({ data }) => {
          commit('SET_DATA_DETAIL', data.data)
          // console.log('SET_DATA_DETAIL = ' + JSON.stringify(data.data))
          commit('SET_LOADING', false)
        }).catch(error => {
          console.log(error)
          commit('SET_LOADING', false)
        })
      }
    },
    simpan ({ commit }, Status) {
      if (Status === 'APV') {
        confirmations.confirmationApprove('hcbp/lanjutSimpan', Status)
      } else if (Status === 'RJT') {
        confirmations.confirmationReject('hcbp/lanjutSimpan', Status)
      }
    },
    async lanjutSimpan ({ commit, state }, Status) {
      if (state.detail.Notes === '' || state.detail.Notes === null) {
        alerts.sweetalertToast('error', 'Note wajib diisi')
        return
      }
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      const formData = new FormData()
      formData.append('NIK', state.detail.NIK)
      formData.append('DokumenPersetujuan', state.DokumenPersetujuan)
      formData.append('Notes', state.detail.Notes)
      formData.append('ApprovalBy', state.detail.ApprovalBy)
      formData.append('ApprovalNo', state.detail.ApprovalNo)
      formData.append('Seq', state.detail.Seq)
      formData.append('Status', Status)
      return await AuthenticationService.postFormData('cms/loan/HCBPUpdate/' + state.detail.id, formData).then(({ data }) => {
        store.dispatch('hcbp/reqData')
        store.dispatch('loadings/setLoading', false)
        // console.log('SET_DATA_DETAIL = ' + JSON.stringify(data.data))
        commit('SET_LOADING', false)
        commit('SET_MODAL', false)
        state.DokumenPersetujuan = ''
        // alert(JSON.stringify(state.DokumenPersetujuan))
        alerts.sweetalertToast('success', 'Berhasil Disimpan')
      }).catch(error => {
        // console.log(error)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
        commit('SET_LOADING', false)
      })
    }
  }
}

export default hcbp
