// import router from '@/router'
import AuthenticationService from './../../services/AuthenticationService'
import alerts from '@/alerts/alert'
import store from '@/store'

const faq = {
  namespaced: true,
  state: {
    data: [],
    dialogEdit: false,
    edit: {},
    loading: false,
    dialogDetail: false,
    dialogCreate: false,
    detail: {}
  },
  getters: {
    getData (state) {
      return state.data
    },
    getDialogEdit (state) {
      return state.dialogEdit
    },
    getDataEdit (state) {
      return state.edit
    },
    getDialogDetail (state) {
      return state.dialogDetail
    },
    getDetail (state) {
      return state.detail
    },
    getDialogCreate (state) {
      return state.dialogCreate
    }
  },
  mutations: {
    SET_DATA (state, data) {
      state.data = data
    },
    SET_DIALOG_EDIT (state, dialogEdit) {
      state.dialogEdit = dialogEdit
    },
    SET_DATA_EDIT (state, data) {
      state.edit = data
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    },
    SET_DIALOG_DETAIL (state, dialogDetail) {
      state.dialogDetail = dialogDetail
    },
    SET_DETAIL (state, detail) {
      state.detail = detail
    },
    SET_DIALOG_CREATE (state, dialogCreate) {
      state.dialogCreate = dialogCreate
    }
  },
  actions: {
    async reqData ({ commit }) {
      commit('SET_LOADING', true)
      return AuthenticationService.get('cms/faq').then(({ data }) => {
        // console.log('req faq = ' + JSON.stringify(data))
        commit('SET_DATA', data.data)
        commit('SET_LOADING', false)
      })
    },
    edit ({ commit }, payload) {
      commit('SET_DATA_EDIT', payload)
      commit('SET_DIALOG_EDIT', true)
      commit('SET_DIALOG_DETAIL', false)
    },
    create ({ commit }) {
      commit('SET_DIALOG_CREATE', true)
      commit('SET_DIALOG_EDIT', false)
      commit('SET_DIALOG_DETAIL', false)
    },
    detail ({ commit }, payload) {
      commit('SET_DETAIL', payload)
      commit('SET_DIALOG_DETAIL', true)
    },
    hapus ({ commit }, id) {
      alerts.questionDelete(id, 'faq/lanjuthapus')
    },
    async lanjuthapus ({ commit }, id) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return await AuthenticationService.get('cms/faq/delete/' + id).then(({ data }) => {
        store.dispatch('faq/reqData')
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', 'Berhasil Dihapus')
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    },
    async save ({ commit }, params) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return AuthenticationService.post('cms/faq/create', params).then(({ data }) => {
        store.dispatch('faq/reqData')
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        commit('SET_DIALOG_CREATE', false)
        alerts.sweetalertToast('success', 'Berhasil Disimpan')
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        commit('SET_DIALOG_CREATE', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    },
    async update ({ commit, state }) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return AuthenticationService.post('cms/faq/update/' + state.edit.id, state.edit).then(({ data }) => {
        store.dispatch('faq/reqData')
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        commit('SET_DIALOG_EDIT', false)
        alerts.sweetalertToast('success', 'Berhasil diUpdate')
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        commit('SET_DIALOG_EDIT', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    }
  }
}

export default faq
