import axios from 'axios'
import AuthenticationService from './../../services/AuthenticationService'
import store from '@/store'
import alerts from '@/alerts/alert'

const profile = {
  namespaced: true,
  state: {
    profile: {},
    form: {
      old_password: '',
      new_password: '',
      password_confirmation: ''
    },
    modal: false
  },
  getters: {
    getProfile (state) {
      return state.captcha
    }
  },
  mutations: {
    SET_PROFILE (state, profile) {
      state.profile = profile
    },
    SET_MODAL (state, data) {
      state.modal = data
    },
    SET_FORM_NULL (state) {
      state.form = {
        old_password: '',
        new_password: '',
        password_confirmation: ''
      }
    }
  },
  actions: {
    reqProfile ({ commit }) {
      return axios.get(`${process.env.VUE_APP_URL}api/v1/get-key/math`).then(({ data }) => {
        commit('SET_PROFILE', data)
      })
    },
    cangePassword ({ commit }) {
      commit('SET_MODAL', true)
    },
    updatePassword ({ commit }, payload) {
      alerts.questionConfirmation(payload, 'profile/lanjutUpdatePassword', 'Apakah data sudah benar ?', '', 'Benar')
    },
    async lanjutUpdatePassword ({ commit, state }) {
      // return await axios.post(`${process.env.VUE_APP_URL}change-password`, params).then(({ data }) => {
      //   console.log(JSON.stringify(data))
      //   commit('SET_MODAL', true)
      // })
      store.dispatch('loadings/setLoading', true)
      return await AuthenticationService.post('change-password', state.form).then(({ data }) => {
        console.log(JSON.stringify(data))
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast(data.icon, data.status)
        if (data.code === 200) {
          commit('SET_MODAL', false)
          commit('SET_FORM_NULL')
        }
      })
    }
  }
}

export default profile
