import router from '@/router'
import AuthenticationService from './../../services/AuthenticationService'
import alerts from '@/alerts/alert'
import store from '@/store'

const product = {
  namespaced: true,
  state: {
    data: [],
    detail: {},
    loading: false,
    detailRate: {
      ProductID: '',
      ProductName: ''
    },
    dataDetailRate: []
  },
  getters: {
    getData (state) {
      return state.data
    },
    getDataDetail (state) {
      return state.detail
    },
    getDetailRate (state) {
      return state.detailRate
    },
    getDataDetailRate (state) {
      return state.dataDetailRate
    }
  },
  mutations: {
    SET_DATA (state, data) {
      state.data = data
    },
    SET_DATA_DETAIL (state, detail) {
      state.detail = detail
    },
    SET_DETAIL_RATE (state, detail) {
      state.detailRate = detail
    },
    SET_DATA_DETAIL_RATE (state, detail) {
      state.dataDetailRate = detail
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    }
  },
  actions: {
    async reqData ({ commit }) {
      commit('SET_LOADING', true)
      return AuthenticationService.get('cms/product').then(({ data }) => {
        commit('SET_DATA', data.data)
        commit('SET_LOADING', false)
      })
    },
    async create ({ commit }, payload) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return AuthenticationService.post('cms/product/create', payload).then(({ data }) => {
        commit('SET_LOADING', false)
        alerts.sweetalertToast('success', data.status)
        store.dispatch('loadings/setLoading', false)
        router.push({ name: 'Product' })
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    },
    async detail ({ commit, state }, payload) {
      commit('SET_DATA_DETAIL_RATE', [])
      commit('SET_DETAIL_RATE', payload)
      commit('SET_LOADING', true)
      return AuthenticationService.get('cms/productrate/' + state.detailRate.ProductID).then(({ data }) => {
        commit('SET_DATA_DETAIL_RATE', data.data)
        // console.log(JSON.stringify(data.data))
        commit('SET_LOADING', false)
      }).catch(error => {
        console.log(error)
        commit('SET_LOADING', false)
      })
    },
    async createProductRate ({ commit, state }, payload) {
      commit('SET_LOADING', true)
      return AuthenticationService.post('cms/productrate/create', payload).then(({ data }) => {
        commit('SET_LOADING', false)
        alerts.sweetalertToast('success', data.status)
        router.push({ name: 'ProductDetail', params: state.detailRate })
      }).catch(error => {
        commit('SET_LOADING', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    },
    async edit ({ commit }, payload) {
      commit('SET_DATA_DETAIL', payload)
      router.push({ name: 'ProductEdit' })
    },
    async update ({ commit, state }, payload) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return AuthenticationService.post('cms/product/update/' + state.detail.id, payload).then(({ data }) => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', data.status)
        router.push({ name: 'Product' })
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    },
    hapus ({ commit }, ProductID) {
      alerts.questionDelete(ProductID, 'product/lanjuthapus')
    },
    async lanjuthapus ({ commit }, ProductID) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return await AuthenticationService.get('cms/product/delete/' + ProductID).then(({ data }) => {
        store.dispatch('product/reqData')
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', data.status)
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    },
    hapusProductRate ({ commit }, ID) {
      alerts.questionDelete(ID, 'product/lanjuthapusProductRate')
    },
    async lanjuthapusProductRate ({ commit, state }, ID) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return await AuthenticationService.get('cms/productrate/delete/' + ID).then(({ data }) => {
        store.dispatch('product/detail', { ProductID: state.detailRate.ProductID, ProductName: state.detailRate.ProductName })
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', 'Berhasil Dihapus')
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    }
  }
}

export default product
