import Vue from 'vue'
import VueRouter from 'vue-router'
import goTo from 'vuetify/lib/services/goto'
import store from '@/store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Index',
    redirect: to => {
      return 'Dashboard'
    }
  },
  {
    path: '*',
    name: '*',
    component: () => import('@/views/Notfound.vue')
  },
  {
    path: '/',
    component: () => import('@/layouts/Auth.vue'),
    meta: { requiresAuth: false },
    children: [
      {
        path: 'login',
        name: 'Login',
        component: () => import('@/views/LoginView.vue')
      },
      {
        path: 'Forgot-password',
        name: 'ForgotPassword',
        component: () => import('@/views/ForgotPassword.vue')
      }
    ]
  },
  {
    path: '/',
    component: () => import('@/layouts/Admin.vue'),
    meta: { requiresAuth: true },
    children: [
      {
        path: 'Dashboard',
        name: 'Dashboard',
        component: () => import('@/views/Dashboard.vue')
      },
      {
        path: 'Approval',
        name: 'Approval',
        component: () => import('@/views/Approval.vue')
      },
      {
        path: 'Product',
        name: 'Product',
        component: () => import('@/views/Product.vue')
      },
      {
        path: '/product-detail/:ProductID/:ProductName',
        name: 'ProductDetail',
        component: () => import('@/views/ProductDetail.vue')
      },
      {
        path: '/Product-create-rate/:ProductID',
        name: 'AddProductRate',
        component: () => import('@/views/AddProductRate.vue')
      },
      {
        path: '/Product-create',
        name: 'AddProduct',
        component: () => import('@/views/AddProduct.vue')
      },
      {
        path: 'Product',
        name: 'ProductEdit',
        component: () => import('@/views/ProductEdit.vue')
      },
      {
        path: 'GeneralSetting',
        name: 'GeneralSetting',
        component: () => import('@/views/GeneralSetting.vue')
      },
      {
        path: '/General-update/:id',
        name: 'Generaltupdate',
        component: () => import('@/components/General/Generalupdate.vue')
      },
      {
        path: '/General-create',
        name: 'Generalcreate',
        component: () => import('@/components/General/Generalcreate.vue')
      },
      {
        path: 'Promotion',
        name: 'Promotion',
        component: () => import('@/views/Promotion.vue')
      },
      {
        path: 'Promotion-create',
        name: 'Promotioncreate',
        component: () => import('@/components/Promotion/Promotioncreate.vue')
      },
      {
        path: 'blog',
        name: 'Blog',
        component: () => import('@/views/Blog.vue')
      },
      {
        path: 'Organization',
        name: 'Organization',
        component: () => import('@/views/Organization.vue')
      },
      {
        path: 'Faq',
        name: 'Faq',
        component: () => import('@/views/Faq.vue')
      },
      {
        path: 'DataHCBP',
        name: 'DataHCBP',
        component: () => import('@/views/DataHCBP.vue')
      },
      {
        path: '/HCBP',
        name: 'HCBP',
        component: () => import('@/views/HCBP.vue')
      },
      {
        path: 'DataEmployee',
        name: 'DataEmployee',
        component: () => import('@/views/DataEmployee.vue')
      },
      {
        path: 'DataIuran',
        name: 'DataIuran',
        component: () => import('@/views/DataIuran.vue')
      },
      {
        path: '/HCComben',
        name: 'HCComben',
        component: () => import('@/views/HCComben.vue')
      },
      {
        path: 'HCCombenupdate',
        name: 'HCCombenupdate',
        component: () => import('@/views/HCCombenupdate.vue')
      },
      {
        path: 'about',
        name: 'About',
        component: () => import('@/views/AboutView.vue')
      },
      {
        path: '/FundingBatch',
        name: 'FundingBatch',
        component: () => import('@/views/FundingBatch.vue')
      },
      {
        path: '/FundingBatch/:id',
        name: 'FundingShow',
        component: () => import('@/components/FundingBatch/Detail.vue')
      },
      {
        path: '/loan',
        name: 'Loan',
        component: () => import('@/views/Loan.vue')
      },
      {
        path: '/Pretermination',
        name: 'Pretermination',
        component: () => import('@/views/Pretermination.vue')
      },
      {
        path: '/Transfer',
        name: 'Transfer',
        component: () => import('@/views/Transfer.vue')
      },
      {
        path: '/Profile',
        name: 'Profile',
        component: () => import('@/views/Profile.vue')
      },
      {
        path: '/Amortisasi',
        name: 'Amortisasi',
        component: () => import('@/views/Amortisasi.vue')
      }
    ]
  },
  {
    path: '/logout',
    name: 'Logout',
    meta: { requiresAuth: true, logout: true }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior: (to, from, savedPosition) => {
    let scrollTo = 0
    if (to.hash) {
      scrollTo = to.hash
    } else if (savedPosition) {
      scrollTo = savedPosition.y
    }
    return goTo(scrollTo)
  }
})

router.beforeEach((to, from, next) => {
  if (to.matched.some((record) => record.meta.requiresAuth)) {
    if (to.matched.some((record) => record.meta.logout)) {
      store.dispatch('auth/LogOut')
      window.location.href = '/login'
      // next('/login')
    } else {
      if (store.getters['auth/isAuthenticated']) {
        next()
      } else {
        store.dispatch('auth/LogOut')
        window.location.href = '/login'
        // next('/login')
      }
    }
  } else {
    if (store.getters['auth/isAuthenticated']) {
      next('/Dashboard')
    } else {
      store.dispatch('auth/LogOut')
      next()
    }
  }
})

export default router
