import store from '@/store'
import alerts from '@/alerts/alert'
import confirmations from './confirmations'
import AuthenticationService from '../../services/AuthenticationService'

const hccomben = {
  namespaced: true,
  state: {
    data: [],
    loading: true,
    modal: false,
    detail: {
      FeeAdmin: '0',
      LoanAmount: '0'
    },
    action: '',
    params: {}
  },
  getters: {
    getData (state) {
      return state.data
    }
  },
  mutations: {
    SET_DATA (state, data) {
      state.data = data
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    },
    SET_DATA_DETAIL (state, detail) {
      state.detail = detail
    },
    SET_MODAL (state, modal) {
      state.modal = modal
    },
    SET_ACTION (state, action) {
      state.action = action
    }
  },
  actions: {
    async reqData ({ commit }) {
      const params = {
        id: store.getters['auth/user'].id,
        id_company: store.getters['auth/user'].companyID
      }
      commit('SET_LOADING', true)
      return AuthenticationService.post('cms/loan/HCCombenRecommendationList', params).then(({ data }) => {
        // console.log('hccomben = ' + JSON.stringify(data))
        commit('SET_DATA', data.data)
        commit('SET_LOADING', false)
      })
    },
    async detail ({ commit }, payload) {
      commit('SET_ACTION', 'Detail')
      await store.dispatch('hccomben/first', payload)
    },
    async edit ({ commit }, payload) {
      commit('SET_ACTION', 'Edit')
      await store.dispatch('hccomben/first', payload)
    },
    async first ({ commit, state }, payload) {
      if (state.detail.id === payload.id) {
        commit('SET_MODAL', true)
      } else {
        commit('SET_DATA_DETAIL',
          {
            FeeAdmin: '0',
            LoanAmount: '0'
          }
        )
        commit('SET_LOADING', true)
        commit('SET_MODAL', true)
        return await AuthenticationService.get('cms/loan/HCCombenRecommendationShow/' + payload.id).then(({ data }) => {
          commit('SET_DATA_DETAIL', data.data)
          // console.log('SET_DATA_DETAIL = ' + JSON.stringify(data.data))
          commit('SET_LOADING', false)
        }).catch(error => {
          console.log(error)
          commit('SET_LOADING', false)
        })
      }
    },
    simpan ({ commit, state }) {
      if (state.detail.HC_Recommendations === '0') {
        confirmations.confirmationReject('hccomben/lanjutkanSimpan', '', 'Apakah yakin mau di Reject ?', 'Cek kembali rekomendasi pinjaman.', ', Reject!')
      } else {
        confirmations.confirmationApprove('hccomben/lanjutkanSimpan', '_')
      }
    },
    async lanjutkanSimpan ({ commit, state }, id) {
      const params = {
        NIK: state.detail.NIK,
        RekomendasiPeminjaman: state.detail.HC_Recommendations,
        DSR: state.detail.DSR,
        SP: state.detail.SP,
        Notes: state.detail.Notes_HCRecommendation,
        ApprovalBy: state.detail.ApprovalBy,
        ApprovalNo: state.detail.ApprovalNo,
        Seq: state.detail.Seq,
        pta1: state.detail.pta1,
        pta2: state.detail.pta2
      }
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return await AuthenticationService.post('cms/loan/HCCombenRecommendationUpdate/' + state.detail.id, params).then(({ data }) => {
        store.dispatch('hccomben/reqData')
        commit('SET_LOADING', false)
        commit('SET_MODAL', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', data.status)
      }).catch(error => {
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
        commit('SET_LOADING', false)
      })
    },
    changePTA ({ commit, state }, condition) {
      if (!condition) {
        state.detail.pta1 = 0
      }
    },
    changeOS ({ commit, state }, condition) {
      if (!condition) {
        state.detail.pta2 = 0
      }
    }
  }
}

export default hccomben
