import AuthenticationService from './../../services/AuthenticationService'
import alerts from '@/alerts/alert'
import store from '@/store'
import confirmations from './confirmations'

const transfer = {
  namespaced: true,
  state: {
    data: [],
    loading: false
  },
  getters: {
    getData (state) {
      return state.data
    }
  },
  mutations: {
    SET_DATA (state, data) {
      state.data = data
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    }
  },
  actions: {
    async reqData ({ commit }) {
      commit('SET_LOADING', true)
      return AuthenticationService.get('cms/loan/TransferBatch').then(({ data }) => {
        // console.log(JSON.stringify(data))
        commit('SET_DATA', data.data)
        commit('SET_LOADING', false)
      })
    },
    paid ({ commit, state }, param) {
      confirmations.confirmationApprove('transfer/ProsesPaid', param)
    },
    async ProsesPaid ({ commit, state }, ID) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return AuthenticationService.get('cms/loan/TransferBatchPaid/' + ID).then(({ data }) => {
        commit('SET_LOADING', false)
        const index = state.data.findIndex(x => x.id === ID)
        state.data[index].status = 'FUN'
        store.dispatch('fundingbatch/reqData')
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', data.status)
      }).catch(error => {
        // console.log(error)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
        commit('SET_LOADING', false)
      })
    }
  }
}

export default transfer
