// import router from '@/router'
import AuthenticationService from './../../services/AuthenticationService'
const approval = {
  namespaced: true,
  state: {
    data: [],
    users: [],
    loading: false,
    detail: {},
    modal: false
  },
  getters: {
    getData (state) {
      return state.data
    },
    getDetail (state) {
      return state.detail
    },
    getModalDetail (state) {
      return state.modal
    }
  },
  mutations: {
    SET_DATA_USERS (state, data) {
      state.users = data
    },
    SET_DATA (state, data) {
      state.data = data
    },
    SET_DETAIL (state, detail) {
      state.detail = detail
    },
    SET_BOOL_MODAL (state, bool) {
      state.modal = bool
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    }
  },
  actions: {
    async reqDataUser ({ commit }) {
      commit('SET_LOADING', true)
      return await AuthenticationService.get('cms/Amortisasi/data-user').then(({ data }) => {
        // console.log('data user = ' + JSON.stringify(data))
        commit('SET_DATA_USERS', data)
        commit('SET_LOADING', false)
      })
    },
    async getData ({ commit }, payload) {
      commit('SET_LOADING', true)
      return await AuthenticationService.post('cms/Amortisasi/data-amortisasi/', payload).then(({ data }) => {
        // console.log('data amortisasi = ' + JSON.stringify(data))
        commit('SET_DATA', data)
        commit('SET_LOADING', false)
      })
    },
    setNullData ({ state }) {
      state.data = []
    },
    dialogClose ({ commit }) {
      commit('SET_BOOL_MODAL', false)
    }
  }
}

export default approval
