// import router from '@/router'
import AuthenticationService from './../../services/AuthenticationService'
import store from '@/store'
import alerts from '@/alerts/alert'
import confirmations from './confirmations'

const fundingbatch = {
  namespaced: true,
  state: {
    data: [],
    detail: [],
    loading: true,
    formApproval: {
      dialog: false,
      detail: {},
      file: null,
      pvcode_m: null,
      action: ''
    }
  },
  getters: {
    getData (state) {
      return state.data
    },
    getDetail (state) {
      return state.detail
    }
  },
  mutations: {
    SET_DATA (state, data) {
      state.data = data
    },
    SET_DETAIL (state, detail) {
      state.detail = detail
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    },
    SET_FORM (state, data) {
      state.formApproval = data
    }
  },
  actions: {
    async reqData ({ commit }, payload) {
      commit('SET_LOADING', true)
      return AuthenticationService.get('cms/loan/FundingBatch/' + store.getters['auth/user'].id).then(({ data }) => {
        // console.log('fundingbatch = ' + JSON.stringify(data))
        commit('SET_DATA', data.data)
        commit('SET_LOADING', false)
      })
    },
    async show ({ commit }, pvcode) {
      commit('SET_LOADING', true)
      commit('SET_DETAIL', [])
      return AuthenticationService.get('cms/loan/FundingBatchShow/' + pvcode).then(({ data }) => {
        commit('SET_DETAIL', data.data)
        commit('SET_LOADING', false)
      })
    },
    async formApproval ({ commit }, items) {
      commit('SET_FORM', {
        dialog: true,
        detail: items,
        file: null,
        pvcode_m: null,
        action: ''
      })
    },
    proses ({ commit }, param) {
      if (param === 'APV') {
        confirmations.confirmationApprove('fundingbatch/lanjtkanProses', param)
      } else if (param === 'RJT') {
        confirmations.confirmationReject('fundingbatch/lanjtkanProses', param)
      }
    },
    async lanjtkanProses ({ commit, state }, action) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      const params = {
        pvcode: state.formApproval.detail.pvcode,
        ApprovalBy: state.formApproval.detail.NextApproval,
        Seq: state.formApproval.detail.Seq,
        pvcode_m: state.formApproval.detail.pvcode_m,
        action: action,
        doc_pv: state.formApproval.detail.doc_pv
      }
      return AuthenticationService.post('cms/loan/FundingBatchApproval/' + state.formApproval.detail.pvcode, params).then(({ data }) => {
        commit('SET_LOADING', false)
        state.formApproval.dialog = false
        const index = state.data.findIndex(x => x.pvcode === state.formApproval.detail.pvcode)
        state.data.splice(index, 1)
        store.dispatch('fundingbatch/reqData')
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', data.status)
      }).catch(error => {
        // console.log(error)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
        commit('SET_LOADING', false)
      })
    },
    download ({ commit }, payload) {
      alert('belum bisa')
    }
  }
}

export default fundingbatch
