import AuthenticationService from './../../services/AuthenticationService'
import utils from '@/utils/util'

const dashboard = {
  namespaced: true,
  state: {
    data: {
      karyawanaktif: {
        active: 0,
        borrower: 0,
        bulanini: 0
      },
      pinjamantersalurkan: {
        bulanini: 0,
        ratarata: 0
      },
      pinjamanproses: {
        jumlah: 0,
        ratarata: 0
      },
      hcbp: 0,
      hccomben: 0,
      loanapproval: 0,
      loanapproved: 0,
      graphic: {
        jumlahpeminjam: [],
        jumlahtersalurkan: [],
        jumlahpeminjambytenor: []
      },
      pinjamanbaru: [],
      pinjamandalamproses: [],
      approvedloan: []
    },
    loading: false
  },
  getters: {
    getData (state) {
      return state.data
    }
  },
  mutations: {
    SET_DATA (state, data) {
      state.data = data
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    }
  },
  actions: {
    async reqData ({ commit }) {
      commit('SET_LOADING', true)
      return await AuthenticationService.post('cms/dashboard', { year: utils.year() }).then(({ data }) => {
        commit('SET_DATA', data.data)
        commit('SET_LOADING', false)
      })
    }
  }
}

export default dashboard
