// import router from '@/router'
import AuthenticationService from './../../services/AuthenticationService'
import alerts from '@/alerts/alert'
import store from '@/store'

const datahcbp = {
  namespaced: true,
  state: {
    data: [],
    edit: {},
    file: null,
    loading: true,
    dialog: false
  },
  getters: {
    getData (state) {
      return state.data
    },
    getDataEdit (state) {
      return state.edit
    },
    getDialog (state) {
      return state.dialog
    }
  },
  mutations: {
    SET_DATA (state, data) {
      state.data = data
    },
    SET_DATA_EDIT (state, data) {
      state.edit = data
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    },
    SET_DIALOG (state, dialog) {
      state.dialog = dialog
    }
  },
  actions: {
    async reqData ({ commit }) {
      commit('SET_LOADING', true)
      return AuthenticationService.get('cms/HCBP').then(({ data }) => {
        commit('SET_DATA', data.data)
        commit('SET_LOADING', false)
      })
    },
    hapus ({ commit }, id) {
      alerts.questionDelete(id, 'datahcbp/lanjuthapus')
    },
    async lanjuthapus ({ commit }, id) {
      store.dispatch('loadings/setLoading', true)
      commit('SET_LOADING', true)
      return await AuthenticationService.get('cms/HCBP/delete/' + id).then(({ data }) => {
        store.dispatch('datahcbp/reqData')
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', 'Berhasil Dihapus')
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    },
    async upload ({ commit, state }) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      const data = new FormData()
      data.append('import_file', state.file)
      return await AuthenticationService.postFormData('cms/HCBP/bulk', data).then(({ data }) => {
        store.dispatch('datahcbp/reqData')
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', 'Berhasil Diupload')
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    }
  }
}

export default datahcbp
