const modalFile = {
  namespaced: true,
  state: {
    dialog: false,
    file: ''
  },
  getters: {
    getDialog (state) {
      return state.dialog
    },
    getFile (state) {
      return state.file
    }
  },
  mutations: {
    SET_DIALOG (state, dialog) {
      state.dialog = dialog
    },
    SET_FILE (state, file) {
      state.file = file
    }
  },
  actions: {
    setDialog ({ commit }) {
      commit('SET_DIALOG', true)
    },
    async setFile ({ commit }, payload) {
      commit('SET_DIALOG', true)
      await commit('SET_FILE', payload)
    }
  }
}

export default modalFile
