import router from '@/router'
import AuthenticationService from './../../services/AuthenticationService'
import alerts from '@/alerts/alert'
import store from '@/store'

const promotion = {
  namespaced: true,
  state: {
    data: [],
    edit: {},
    loading: false,
    detail: {},
    dialog: false,
    dialogEdit: false
  },
  getters: {
    getData (state) {
      return state.data
    },
    getDataEdit (state) {
      return state.edit
    },
    getDetail (state) {
      return state.detail
    },
    getDialog (state) {
      return state.dialog
    },
    getDialogEdit (state) {
      return state.dialogEdit
    }
  },
  mutations: {
    SET_DATA (state, data) {
      state.data = data
    },
    SET_DATA_EDIT (state, data) {
      state.edit = data
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    },
    SET_DETAIL (state, detail) {
      state.detail = detail
    },
    SET_DIALOG (state, dialog) {
      state.dialog = dialog
    },
    SET_DIALOG_EDIT (state, dialog) {
      state.dialogEdit = dialog
    }
  },
  actions: {
    async reqData ({ commit }) {
      commit('SET_LOADING', true)
      return AuthenticationService.get('cms/promotion').then(({ data }) => {
        commit('SET_DATA', data.data)
        commit('SET_LOADING', false)
      })
    },
    async simpan ({ commit }, payload) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return AuthenticationService.post('cms/promotion/create', payload).then(({ data }) => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', 'Berhasil disimpan.')
        router.push({ name: 'Promotion' })
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    },
    hapus ({ commit }, id) {
      alerts.questionDelete(id, 'promotion/lanjuthapus')
    },
    detail ({ commit }, data) {
      commit('SET_DETAIL', data)
      commit('SET_DIALOG', true)
    },
    closeModal ({ commit }) {
      commit('SET_DIALOG', false)
    },
    edit ({ commit }, data) {
      store.dispatch('product/reqData')
      commit('SET_DATA_EDIT', data)
      commit('SET_DIALOG_EDIT', true)
      commit('SET_DIALOG', false)
    },
    async update ({ commit, state }) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return AuthenticationService.post('cms/promotion/update/' + state.edit.id, state.edit).then(({ data }) => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', 'Berhasil diupdate.')
        commit('SET_DIALOG_EDIT', false)
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    },
    closeModalEdit ({ commit }) {
      commit('SET_DIALOG_EDIT', false)
      store.dispatch('promotion/reqData')
    },
    async lanjuthapus ({ commit }, id) {
      commit('SET_LOADING', true)
      store.dispatch('loadings/setLoading', true)
      return await AuthenticationService.get('cms/promotion/delete/' + id).then(({ data }) => {
        store.dispatch('promotion/reqData')
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('success', 'Berhasil Dihapus')
      }).catch(error => {
        commit('SET_LOADING', false)
        store.dispatch('loadings/setLoading', false)
        alerts.sweetalertToast('error', error.response.data.message || error.response.data.error)
      })
    }
  }
}

export default promotion
